@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
article.simple{background-color:tan;box-sizing:border-box;box-shadow:rgba(0,0,0,0.35) 0px 5px 15px;border-radius:5px;margin:1rem;padding:1rem;max-width:700px}article.simple div.deck__infos{display:flex;justify-content:space-between;margin:0.2rem 1rem}

.flashcard{background-color:tan;box-shadow:0px 3px 3px -2px rgba(0,0,0,0.2),0px 3px 4px 0px rgba(0,0,0,0.14),0px 1px 8px 0px rgba(0,0,0,0.12);margin:1rem;max-width:60%;min-width:800px;display:flex;align-content:space-between;justify-content:space-between}.flashcard .flashcard__sections{width:100%;height:100%}.flashcard .flashcard__sections>section{border-bottom:none}.flashcard .flashcard__sections>section p:first-of-type{color:rgba(0,0,0,0.4)}.flashcard .flashcard__sections hr{border:none;margin:1rem;border-bottom:0.1rem solid rgba(128,128,128,0.5)}.flashcard__buttons{background:linear-gradient(to left, #cb997e, rgba(146,107,85,0.662745));padding:1rem;display:flex;flex-direction:column;align-items:center;justify-items:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.flashcard__button{border:none;border-radius:5px;margin:3px;padding:0.3rem;box-sizing:border-box;background-color:transparent;display:flex;flex-direction:column;align-items:center;width:100%}.flashcard__button:hover{background-color:rgba(255,255,255,0.2)}.flashcard__button svg{font-size:1.5rem}.flashcard__button span{font-size:0.8rem;color:black}

*{box-sizing:border-box;font-family:'Roboto', Helvetica, Arial, 'Courier New';color:black}body{margin:0;padding:0}main.centered-content{display:flex;align-items:center;flex-direction:column;padding:1rem}main.centered-content h1,main.centered-content h2,main.centered-content h3,main.centered-content h4,main.centered-content h5,main.centered-content h6{align-self:flex-start}a{text-decoration:none}a h1:hover,a h2:hover,a h3:hover,a h4:hover,a h5:hover,a h6:hover{color:gold;text-decoration:underline}h1,h2,h3,h4,h5,h6{text-transform:uppercase;padding:1rem;margin:0;color:black}h1{font-size:5rem}h2{font-size:3.5rem}h3{font-size:3rem}h4{font-size:2.5rem}h5{font-size:1.6rem}h6{font-size:1.2rem;margin-bottom:0.2rem}header{background-color:#71816d;padding:0.3rem;width:100%;padding-left:1rem;padding-right:1rem;display:flex;align-items:center;justify-content:space-around}footer{background-color:black;display:flex;align-items:center;justify-content:center;padding-top:0.5rem;padding-bottom:1.2rem;max-height:15vh}.circular-img{width:3rem;height:3rem;padding-left:1rem;padding-right:1rem;background-color:rgba(29,185,84,0.8);border-radius:50%}.circular-img:hover{background-color:#eca845}button.iconed{border:none;border-radius:5px;padding:15px;box-sizing:border-box;width:3rem;height:3rem;display:flex;flex-direction:column;align-items:center;justify-content:center}button.iconed svg{font-size:1.5rem}button.iconed span{font-size:0.5rem;color:black}button.iconed:hover{background-color:seagreen}button.transparent{background-color:transparent;border:none}button.transparent *:hover{color:white}

.treeContainer{color:#3e3e3e;min-width:100vw;min-height:80vh;display:flex;flex-direction:row;font-family:'Roboto Mono', 'Helvetica Neue', sans-serif;padding:4rem}.treeContainer .vertical-linker{width:2rem;min-height:70%;background:transparent;display:flex;flex-direction:column;align-items:center}.treeContainer .vertical-linker span{border-right:solid cornflowerblue 2px;width:100%;height:100%}.treeContainer .branch{display:flex;padding-bottom:0.5rem}.treeContainer .trunk{display:flex;flex-direction:column;align-items:center;align-items:stretch}.treeContainer .horizontal-linker{background-color:transparent;width:1rem;height:1rem;display:flex;flex-direction:row;align-items:center;justify-items:center}.treeContainer .horizontal-linker span{background-color:cornflowerblue;width:100%;height:2px}.treeContainer .file{display:flex;flex-wrap:wrap;align-items:center;justify-items:flex-start;background:transparent}.treeContainer .dir{background-color:royalblue}

html {
  justify-self: right;
  background-color: #F1F7ED;
}

.dashboard-wrapper {
  background-color: seagreen;
  width: 100vw;
  height: 100vh;
}
.dashboard-buttons {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
}
form{margin:0;padding:2rem;display:flex;flex-direction:column}form div.control-buttons{display:flex;justify-content:flex-start}form div.control-buttons button.control{padding:6px 16px;margin-right:4px;border-radius:5px;text-transform:uppercase}form div.control-buttons button.submit{color:red;border-radius:5px;margin-right:1rem;margin-left:auto}form div.control-buttons button:hover{background-color:saddlebrown}form input,form select{outline:none;background-color:rgba(241,224,197,0.7);background-color:transparent;border:none;border-bottom:1px solid black;font-size:1rem;margin:0.3rem 0 1rem 0}form label,form input{font-family:'Courier';font-size:inherit}form fieldset{border:none;all:unset;border-color:'black';display:flex;flex-direction:column;margin:0.5rem}form fieldset fieldset{background-color:#71816d;padding:5px}

